<template>
  <div id="app">
    <HeaderComponent />
    <FooterComponent />
  </div>
</template>

<script>
import HeaderComponent from './components/HeaderComponent.vue';
import FooterComponent from './components/FooterComponent.vue';

export default {
  name: 'App',
  components: {
    HeaderComponent,
    FooterComponent
  },
  mounted() {
    window.onbeforeunload = function () {
      window.scrollTo(0, 0);
    }
    const gradientTitles = document.querySelectorAll('.gradient-title');
    
    const observer = new IntersectionObserver((entries, observer) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('visible');
          observer.unobserve(entry.target); // Arrête d'observer une fois que l'animation a été appliquée
        }
      });
    }, {
      threshold: 0.5 // Le seuil à partir duquel on veut lancer l'animation
    });

    gradientTitles.forEach(title => {
      observer.observe(title);
    });
  }
};
</script>

