<template>
  <div class="menu-view">
    <div class="header">
      <h1 class="title gradient-title">La Carte</h1>
    </div>
    <div class="menu-content">
      <div class="main-menu" @click="openFullScreen(menu1)">
        <img :src="menu1" alt="Menu Principal" class="menu-image" />
      </div>
      <div class="drinks-menu" @click="openFullScreen(menu2)">
        <img :src="menu2" alt="Carte des Boissons" class="menu-image" />
      </div>
    </div>

    <div v-if="isModalOpen" class="modal" @click="closeFullScreen">
      <span class="close-button">&times;</span>
      <img :src="currentMenu" class="modal-content" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'MenuView',
  data() {
    return {
      menu1: require('@/assets/menu1.jpg'),
      menu2: require('@/assets/menu2.jpg'),
      isModalOpen: false,
      currentMenu: '',
    };
  },
  methods: {
    openFullScreen(menu) {
      this.currentMenu = menu;
      this.isModalOpen = true;
    },
    closeFullScreen() {
      this.isModalOpen = false;
      this.currentMenu = '';
    },
  },
};
</script>

<style scoped>
.menu-view {
  margin-top: 20px;
  padding: 0 15px;
}

.menu-content {
  display: flex;
  gap: 20px;
}

.main-menu {
  flex: 6;
  background-color: #f8f8f8;
  padding: 10px;
  border-radius: 10px;
  cursor: pointer;
}

.drinks-menu {
  flex: 3;
  background-color: #f1f1f1;
  padding: 10px;
  border-radius: 10px;
  cursor: pointer;
}

.menu-image {
  width: 100%;
  border-radius: 10px;
  object-fit: cover;
}

.modal {
  display: flex;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  justify-content: center;
  align-items: center;
}

.modal-content {
  max-width: 90%;
  max-height: 90%;
  border-radius: 10px;
}

.close-button {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #fff;
  font-size: 40px;
  font-weight: bold;
  cursor: pointer;
}

/* Styles pour les petits écrans */
@media screen and (max-width: 768px) {
  .menu-content {
    flex-direction: column;
    gap: 15px;
  }

  .main-menu {
    flex: 6;
  }

  .drinks-menu {
    flex: 3;
  }

  .menu-image {
    height: auto;
  }

  .header h1 {
    font-size: 1.5rem;
  }
}
</style>
